.body {
  width: 100%;
  height: 50px;
  display: grid;
  grid-template-columns: 20% 60% 20%;
  text-align: center;
}
.detail {
  display: grid;
  grid-template-columns: auto auto;
  text-align: center;
}

.link {
  color: white;
  text-decoration: none;
  text-shadow: 2px 2px 4px black;
}

.title:hover {
  color: blueviolet;
}
